import React from "react";

// Components
import Accordion from "./components/Accordion/Container";

// Image
import logo from "./assets/images/Solo_Logo.png";

// Data
import apiInfo from "./data/apiInfo.json";

const App = () => {
  return (
    <div>
      <div className="sanble-welcome">
        <img src={logo} alt="SANBLE" />
        <h1>Welcome to the Sanble API</h1>
      </div>

      {apiInfo.sections.map((section, i) => (
        <Accordion data={{ ...section, key: i }} key={i} />
      ))}
    </div>
  );
};

export default App;
