import React from "react";

// Components
import Endpoint from "./Endpoint";

const Container = ({ data }) => (
  <div className="sanble-accordion arrows">
    <header className="sanble-accordion-box">
      <label htmlFor={`acc-close-${data.key}`} className="box-title">
        {data.title}
      </label>
    </header>

    {data.paths.map((path, i) => (
      <Endpoint path={{ ...path, key: data.key }} key={i} />
    ))}

    {/* Close accordion  */}
    <input type="radio" name="accordion" id={`acc-close-${data.key}`} />
  </div>
);

export default Container;
