import React from "react";

const Endpoint = ({ path }) => (
  <>
    <input type="radio" name="accordion" id={`${path.url}-${path.key}`} />
    <section className="sanble-accordion-box">
      <label className="box-title" htmlFor={`${path.url}-${path.key}`}>
        <span className={`badge ${path.methods}`}>{path.methods}</span>
        {path.url}
      </label>
      <label className="box-close" htmlFor={`acc-close-${path.key}`} />
      <div className="box-content">
        {path.requestBody && (
          <div className="sanble-fields">
            <h4>Body Parameters</h4>
            {path.requestBody.map((field, i) => (
              <div className="field" key={i}>
                <p>
                  {field.name}
                  <small className="required">
                    {field.required ? "Required" : "Optional"}
                  </small>
                </p>
                <div className="description">
                  <p>Description: {field.description}</p>
                  {field.rules.length > 0 && (
                    <>
                      <p style={{ marginBottom: 0 }}>Rules:</p>
                      <ul className="rules">
                        {field.rules.map((rule, j) => (
                          <li key={j}>{rule}</li>
                        ))}
                      </ul>
                    </>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </section>
  </>
);

export default Endpoint;
